@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Robotocondensed.woff2") format("woff2"), url("../fonts/Robotocondensed.woff") format("woff"); }

body {
  font-family: "PT Mono", "Arial", sans-serif; }

a {
  text-decoration: none;
  cursor: pointer; }

input {
  border: none;
  border-radius: 0;
  box-shadow: 0;
  cursor: pointer; }

input:hover {
  opacity: 0.5;
  transition: all ease 0.5s; }

input:focus {
  opacity: 0.5; }

button {
  cursor: pointer; }

a[href]:hover {
  opacity: 0.5;
  transition: all ease 0.5s; }

button:hover {
  opacity: 0.5;
  transition: all ease 0.5s; }

li {
  list-style: none; }

section {
  margin: 0; }

article {
  margin: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.entry {
  background-color: #1b2d37;
  color: #ffffff; }

.entry__head {
  margin-top: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.entry__p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px; }

.entry__button {
  display: block;
  background-color: #00b2ff;
  width: 409px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.entry__scroll {
  width: 93px;
  display: block;
  opacity: 0.5;
  transform: rotate(-90deg);
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px; }

.header {
  display: flex;
  background-color: #1b2d37;
  color: #ffffff; }

.header__p1 {
  font-weight: bold;
  font-size: 25px;
  line-height: 29px; }

.header__p2 {
  color: #788891;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px; }

.header__modal {
  display: none;
  width: 490px;
  background-color: #00b2ff;
  color: #ffffff;
  flex-direction: column; }

.header__form {
  display: flex;
  flex-direction: column; }

.header__name {
  font-family: "Roboto Condensed", "Arial", sans-serif; }

.header__button {
  width: 180px;
  height: 45px;
  border: 1px #ffffff solid;
  background: none;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.header__contacts {
  display: flex;
  justify-content: space-between; }

.header__ul {
  display: flex; }

.header__li {
  font-weight: normal;
  font-size: 16px;
  line-height: 18px; }

.services {
  background-color: #1b2d37;
  color: #ffffff; }

.services__ul {
  display: flex;
  justify-content: space-around; }

.services__a {
  display: block;
  width: 370px;
  height: 620px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px; }

.services__a--production {
  background-image: url(../img/production-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.services__a--complete {
  background-image: url(../img/complete-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.services__a--mounting {
  background-image: url(../img/mounting-desktop.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.services__p {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px; }

.form {
  display: flex;
  justify-content: space-between;
  background-color: #00b2ff;
  color: #ffffff; }

.form__content {
  display: flex;
  flex-direction: column; }

.form__action {
  display: flex;
  flex-direction: column; }

.form__question {
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px; }

.advantages {
  padding-top: 113px;
  color: #666666; }

.advantages__head {
  margin-top: 50px;
  margin-bottom: 0px;
  color: #00b2ff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px; }

.advantages__list {
  width: 1170px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.advantages__item {
  width: 570px;
  padding: 45px;
  cursor: pointer;
  margin-bottom: 30px;
  position: relative;
  box-sizing: border-box;
  border: 1px #ebebeb solid;
  border-left: 2px #00b2ff solid; }

.advantages__item::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
  top: 40px; }

.advantages__item--hired::before {
  width: 35px;
  height: 35px;
  background-image: url(../img/hired.svg); }

.advantages__item--time::before {
  width: 29px;
  height: 35px;
  background-image: url(../img/stopwatch.svg); }

.advantages__item--detective::before {
  width: 36px;
  height: 31px;
  background-image: url(../img/detective.svg); }

.advantages__item--person::before {
  width: 35px;
  height: 35px;
  background-image: url(../img/tag.svg); }

.advantages__description {
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.advantages__item:hover {
  border: none;
  background-color: #00b2ff;
  color: #ffffff; }
  .advantages__item:hover .advantages__head {
    color: #ffffff; }

.advantages__item--person:hover::before {
  width: 35px;
  height: 35px;
  background-image: url(../img/tag-hover.svg); }

.advantages__item--detective:hover::before {
  width: 36px;
  height: 31px;
  background-image: url(../img/detective-hover.svg); }

.advantages__item--time:hover::before {
  width: 29px;
  height: 35px;
  background-image: url(../img/stopwatch-hover.svg); }

.advantages__item--hired:hover::before {
  width: 35px;
  height: 35px;
  background-image: url(../img/hired-hover.svg); }

@media (max-width: 1023px) {
  .advantages__list {
    width: 720px; }
  .advantages__item {
    width: 345px; }
  .advantages__head {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px; }
  .advantages__description {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px; } }

@media (max-width: 767px) {
  .advantages__head {
    font-size: 20px;
    line-height: 140.62%; }
  .advantages__list {
    width: 300px;
    flex-direction: column; }
  .advantages__item {
    width: 290px; } }

.footer__back {
  border-top: 1px solid #626962;
  opacity: 0.5;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px; }

.footer__condition {
  display: flex;
  justify-content: space-around; }

.footer__main {
  display: flex;
  justify-content: space-around; }

.footer__contacts-ul {
  opacity: 0.5;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px; }

.footer__nav-ul {
  opacity: 0.5;
  font-weight: normal;
  font-size: 15px;
  line-height: 26px;
  letter-spacing: 0.03em; }

.footer__nav-a {
  color: #1b2d37; }

.footer__head {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 16px; }

.about-us {
  display: flex; }

.about-us h2 {
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px; }

.about-us p {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px; }

.about-us__a {
  display: block;
  width: 180px;
  height: 45px;
  border: 1px #00b2ff solid;
  color: #00b2ff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase; }
